/*
 * This file contains modification of Bootstrap theme that cannot be achived by modifying _custom.scss
 */

/* Navs */

/* Common */
.nav-item {
  position: relative;
  height: $pstg-navbar-height;

  .nav-link {
    color: $pstg-gray-dark;
    height: $pstg-navbar-height;
    line-height: $pstg-navbar-height;
    padding: 0 $pstg-padding;
    font-size: $pstg-navbar-font-size;
    font-weight: 600;
  }

  &.active .nav-link {
    color: $pstg-gray-darker;

    &::after {
      content: '';
      display: block;
      position: absolute;
      left: $pstg-padding - $pstg-navbar-pill-height / 2;
      right: $pstg-padding - $pstg-navbar-pill-height / 2;
      bottom: 0;
      height: $pstg-navbar-pill-height;
      border-radius: $pstg-navbar-pill-height / 2;
      background-color: $pstg-orange;
    }
  }

  // To avoid potential overflow of the text, the last element does not have a right padding
  &:last-of-type {
    .nav-link {
      padding-right: 0;
    }

    &.active .nav-link::after {
      right: - $pstg-navbar-pill-height / 2;
    }
  }
}


/* Navbar */
.navbar {

  &.navbar-full {
    height: 43px;
    border-bottom: solid 1px $pstg-border-color;
  }

  &.navbar-light {
    background-color: #fff;
  }
}

/* Navtabs */

.nav-tabs {
  background-color: $pstg-gray-lightest;
  border-bottom: solid $pstg-border-width $pstg-border-color;

  .nav-item {
    display: inline-block;
    float: none;
  }
}

.tab-content {
  .list-group .list-group-item {
    border-right: 0;
    border-left: 0;
    line-height: 1.3;
    overflow-wrap: break-word;
  }
}




/* Cards */

.card + .card {
  margin-top: $pstg-padding;
}

.card-header {
  font-size: $pstg-font-border-box-size;
  font-weight: $pstg-font-border-box-weight;
  color: $pstg-font-border-box-color;
  min-height: $pstg-border-box-header-min-height;
  overflow-wrap: break-word;
}


/* Lists */

.list-group-item-action {
  cursor: pointer;
}

.no-disabled-background {
  &.list-group .list-group-item.disabled {
    background-color: $list-group-bg;
  }
}

/* Tables */

@mixin fixed-header-cell-width($weight) {
  min-width: $weight * ( 2 * $pstg-table-cell-padding-x + $pstg-border-width);
  width:     $weight * ( 2 * $pstg-table-cell-padding-x + $pstg-border-width);
  flex-grow: $weight;
  flex-shrink: 0;
}

.table {
  background-color: transparent;

  tbody,
  thead,
  tfoot {
    background-color: white;
  }

  // By default Bootstrap adds a border on top because each td and th have a border-top.
  // Remove for the first row (must be in a thead)
  thead tr:first-of-type th {
    border-top: 0;
  }

  thead th {
    font-size: $pstg-table-head-font-size;
  }

  &.table-bordered {

    // Must have these 3 properties to have a border-radius
    border-collapse: separate;
    border-spacing: 0;
    border-radius: $pstg-border-radius;

    td {
      #list-view-array-name-cell{
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        max-width: $pstg-table-max-array-name-cell-width;
      }
    }

    // Each cell only draws the border bellow and to the right of itself
    th, td {
      border-left: 0;
      border-top: 0;
      border-bottom: $pstg-border-width solid $pstg-table-internal-separator-color;
      border-right: $pstg-border-width solid $pstg-table-internal-separator-color;
    }

    // But the cells in the last row don't draw a border, because there are the borders of <table>
    tr:last-of-type td {
      border-bottom: 0;
    }

    td:last-of-type,
    th:last-of-type {
      border-right: 0;
    }

    tr:first-of-type th:first-of-type {
      border-top-left-radius: $pstg-border-radius - $pstg-border-width;
    }

    tr:first-of-type th:last-of-type {
      border-top-right-radius: $pstg-border-radius - $pstg-border-width;
    }

    tr:last-of-type td:first-of-type {
      border-bottom-left-radius: $pstg-border-radius - $pstg-border-width;
    }

    tr:last-of-type td:last-of-type {
      border-bottom-right-radius: $pstg-border-radius - $pstg-border-width;
    }


    // Remove the right border of the first column
    &.not-first-col td:first-of-type,
    &.not-first-col th:first-of-type {
      border-right: 0;
      padding-right: 0;
    }

  }

  .thead-default {
    background-color: $pstg-gray-lightest;

    tr

    tr,
    th,
    td  {
      font-size: $pstg-font-border-box-size;
      font-weight: $pstg-font-border-box-weight;
      color: $pstg-font-border-box-color;
      background-color: transparent;
    }
  }

  &.table-fixed-header {

    // The table takes the full space of its container
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;

    flex-direction: column; // thead, tbody, tfoot are on top of each-other
    align-items: stretch;   // and they have full width

    thead {
      display: flex;
      flex-direction: column; // tr are on top of each-other
      align-items: stretch;   // and they have full width
      position: relative;
    }

    // Don't you dare shrink, you're going to scroll
    thead,
    tfoot,
    th,
    td {
      flex-shrink: 0;
    }

    tbody {
      overflow-y: scroll;
      display: inline-block;
    }

    thead > tr,
    tbody > tr,
    tfoot > tr {
      display: flex;
      flex-direction: row; // cells are placed on the right side of each other
      flex-wrap: nowrap;  // Don't split one row into multiple rows if there is not the space for all cells
    }

    // Prevent wrapping
    thead th,
    tbody td {
      overflow-x: hidden;
      text-overflow: ellipsis;
      display: inline-block;
      white-space: nowrap;
    }


    // Add class .weight-$i on each cell. The width of each cell is going to be proportional to $i
    @for $i from 1 through 16 {
      .weight-#{$i} {
        @include fixed-header-cell-width($i)
      }
    }

    // By default each cell has weight 1
    td, th {
      @extend .weight-1;
    }

    // The heading doesn't have a scrollbar, but we still need to offset the width by exactly the amount
    // taken by a scrollbar to make the size consistent with the body. So to do this, add an invisible element
    // at the end of the last th that always scrolls.
    thead tr::after {
      content: '';
      display: block;
      overflow-y: scroll;
      visibility: hidden;
    }

    &.table-bordered {
      // When there are not enough rows to fill-up the table, we want to show the borders around the content, not the table
      border: 0;

      thead,
      tbody,
      tfoot {
        border-left: solid 1px $pstg-border-color;
        border-right: solid 1px $pstg-border-color;
      }

      thead {
        border-top: solid 1px $pstg-border-color;
        border-top-left-radius: $pstg-border-radius;
        border-top-right-radius: $pstg-border-radius;
      }

      tfoot,
      tbody {
        border-bottom: solid 1px $pstg-border-color;
        border-bottom-left-radius: $pstg-border-radius;
        border-bottom-right-radius: $pstg-border-radius;
      }

      // Remove tbody border-bottom when there is a tfoot
      tfoot + tbody {
        border-bottom: 0;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
      }
    }
  }

  /* Multiline thead */
  thead tr.multiline {

    // Don't use the general padding so each .column-heading has the size of its column
    th[colspan] .column-headings {
      margin-left: -$pstg-table-cell-padding-x;
      margin-right: -$pstg-table-cell-padding-x;
    }

    th[colspan] .column-heading {
      float: left;
      padding: 0 5px;
    }

    th[colspan="2"] .column-heading {
      width: 50%;
    }
    th[colspan="3"] .column-heading {
      width: 33.33%;
    }
    th[colspan="4"] .column-heading {
      width: 25%;
    }
    th[colspan="5"] .column-heading {
      width: 20%;
    }

    .column-headings {
      height: 30px;
      overflow: hidden;
    }

    .column-title {
      color: $pstg-gray-dark;
      font-size: 12px;
    }

    .column-subtitle {
      position: relative;
      top: -4px;
      color: $pstg-gray-dark;
      font-size: 10px;

    }

    .column-headings input[type="search"] {
      position: relative;
      top: 3px;
      height: 22px;
      color: $pstg-gray-dark;
    }
  }
}

.table-fixed-header-container {
  position: relative;
  overflow-y: hidden;
}


/**** FORMS *****/

label.required::after {
  position: absolute;
  content: "*";
  color: $pstg-orange;
  right: -7px;
}

select {
  cursor: pointer;

  &.custom-select.no-caret {
    background-image: none;
  }

  &.custom-select {
    background-position: right;
  }
}

.btn-group {
  select {
    border: none;
    height: $pstg-btn-height - 2 * $pstg-border-width;
  }
}

.form-check-inline {
  height: $pstg-btn-height;
  line-height: $pstg-btn-height;
}

/* Buttons */

.btn {
  min-width: 105px;
  min-height: $pstg-btn-height;

  &.with-action-icon {
    padding: 0;
    height: $pstg-btn-height;
    min-width: $pstg-btn-height;
    width: $pstg-btn-height;
    position: relative;

    .pstg-action-icon {
      height: $pstg-btn-icon-height;
      position: absolute;
      top: ($pstg-btn-height - $pstg-btn-icon-height) / 2  - $pstg-border-width;
      left: ($pstg-btn-height - $pstg-btn-icon-height) / 2  - $pstg-border-width;
    }

    &.btn-primary .pstg-action-icon {
      fill: $pstg-btn-primary-color;
    }

    &.btn-secondary .pstg-action-icon {
      fill: $pstg-btn-secondary-color;
    }

  }

  &.btn-secondary {
    transition-property: border, box-shadow, color;
  }

  &.btn-secondary.focus, &.btn-secondary:focus, &.btn-secondary:hover {
    background-color: #fff;
    &:not([disabled]) {
      box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
    }
  }

  &.with-caret {
    // by default, caret is down:
    background: $pstg-form-control-caret-down right 0 center no-repeat #fff;
    padding-right: $pstg-form-control-padding-x * 2;

    &.up {
      background-image: $pstg-form-control-caret-up;
    }
  }

}


/* Input */

input.form-control {
  height: $pstg-form-control-height;
  padding-top: 0px;
  padding-bottom: 0px;
}


// The icons are placed absolutely in a position: relative container
.input-with-icon {
  $icon-width: 14px;
  $icon-width-sm: 10px;
  $icon-padding-x: 9px;

  position: relative;

  .pstg-action-icon {
    width: $icon-width;
    height: $icon-width;
    position: absolute;
    z-index: 4;
    top: calc(( #{$input-height} - #{$icon-width} ) / 2 );
    fill: $pstg-gray-light;
  }

  &.before input {
    padding-left: calc(#{$icon-padding-x} + #{$icon-width} + #{$icon-padding-x});
  }

  &.before-sm {
    .icon-before .pstg-action-icon {
      width: $icon-width-sm;
      height: $icon-width-sm;
    }

    input {
      padding-left: calc(#{$icon-padding-x} + #{$icon-width-sm} + #{$icon-padding-x});
    }
  }

  .icon-before .pstg-action-icon {
    left: $icon-padding-x;
  }

  &.after input {
    padding-right: calc(#{$icon-padding-x} + #{$icon-width} + #{$icon-padding-x});
  }

  &.after-sm {
    .icon-after .pstg-action-icon {
      width: $icon-width-sm;
      height: $icon-width-sm;
    }

    input {
      padding-right: calc(#{$icon-padding-x} + #{$icon-width-sm} + #{$icon-padding-x});
    }
  }

  .icon-after .pstg-action-icon {
    right: $icon-padding-x;
  }

  .actionable .pstg-action-icon {
    transition: fill ease-in-out .15s;

    &:hover,
    &:active {
      fill: $pstg-gray-darker;
      cursor: pointer;
    }
  }
}


/* Select */
.custom-select {
  width: 100%;
  height: $pstg-form-control-height;
}

/* Modals */
.modal .modal-footer.btn-toolbar {
  margin: 0; // Remove the btn-toolbar margin when used on the modal-footer
}
